@use "./variable.scss";
@forward './variable.scss';
* {
    margin: 0;
    padding: 0;
}



body {
    @include variable.font-regular;
    @include variable.fontProps(16px, 20px, variable.$textColor);
    background-color: variable.$backgroundPrimary;
}

.primaryBanner {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        left: 35%;
        height: 5px;
        background: variable.$bannerLineColor;
        width: 30%;
        z-index: 0;
        bottom: -10px;
    }
}

h1 {
    margin: 30px 0;
    padding: 10px 0;
    @include variable.fontProps(52px, 58px);
    letter-spacing: 2px;
    &.whiteBanner {
        color: variable.$whiteColor;
    }
}

h3 {
    @include variable.fontProps(36px, 40px);
    &.primaryBanner {
        color: variable.$primaryHeadingColor;
        letter-spacing: 2px;
    }
}
